adas por los activos financieros de renta fija, pues bajo buenas condiciones económicas el valor de la acción sube, lo que conlleva a que la rentabilidad por la compra aumente, pero si el valor de la acción baja, esto podría resultar en la pérdida de los dividendos esperados e incluso en una disminución del valor de la inversión inicial.")]),_c('p',[_vm._v("Otros tipos de activos financieros de renta variable que se encuentran en el mercado, de acuerdo con su origen, condiciones y uso en el mercado financiero son: fondos de inversión, bonos convertibles y mercado de divisas.")])]),_c('div',{staticClass:"tarjeta color-acento-contenido1 p-3 texto-negro",attrs:{"x":"85%","y":"37.5%","numero":"5","tooltip":"Abrir recurso"}},[_c('p',[_vm._v("Son productos financieros en el que los administradores acumulan el dinero de muchos inversores para después invertirlo en otros instrumentos financieros. Bajo estas condiciones, el valor del fondo de inversión y, por tanto, de las rentabilidades que pueda otorgar depende de los activos que se contienen dentro del fondo, y de las rentabilidades que a su vez estas inversiones generen.")])]),_c('div',{staticClass:"tarjeta color-acento-contenido1 p-3 texto-negro",attrs:{"x":"85%","y":"49.5%","numero":"6","tooltip":"Abrir recurso"}},[_c('p',[_vm._v("Son activos financieros que permiten a su poseedor convertirlo en un tiempo determinado en acciones, y con ello pasar a ser accionista de la empresa generadora del instrumento financiero, en el que tiene diferentes derechos y obligaciones, permitiéndole además recibir dividendos producto de las rentabilidades obtenidas por la misma, las cuales pueden variar acorde a las condiciones económicas y financieras de la empresa.")])]),_c('div',{staticClass:"tarjeta color-acento-contenido1 p-3 texto-negro",attrs:{"x":"85%","y":"61.5%","numero":"7","tooltip":"Abrir recurso"}},[_c('p',[_vm._v("Es el mercado que se caracteriza por la libre negociación y cambio de divisas. Su objetivo es el de facilitar e